.App {
    text-align: center;
}
.App-logo {
    height: 40vmin;
    pointer-events: none;
}
.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}
.App-link {
    color: #61dafb;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}
@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

button {
    background: none;
    border: none;
    color: silver;
    font-size: 20px;
    border: 1px solid silver;
    border-radius: 10px;
}
button:hover {
    border: 1px solid white;
    color: white;
}

input {
    background: none;
    border: none;
    color: silver;
    font-size: 20px;
    border: 1px solid silver;
    border-radius: 10px;
    padding-right: 10px;
    text-align: end;
}
input:focus {
    outline: none;
    border: 1px solid white;
    color: white;
}
